* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  width: 100%;
  min-height: 100vh;
  background-color: antiquewhite;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  width: 300px;
  min-height: 400px;
  /* background-color: blueviolet; */
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.72) 0%, rgba(0, 0, 0, 0) 100%),url(https://images.pexels.com/photos/15360894/pexels-photo-15360894/free-photo-of-woman-standing-on-the-beach.jpeg?auto=compress&cs=tinysrgb&w=800&lazy=load);
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  
}

.poster {
  width: 100%;
  min-height: 340px;
  /* background-color: aqua; */
  display: flex;
  align-items: center;
  text-align: center;
  padding: 0 15px;
  box-sizing: border-box;
  color: #fff;
  font-weight: 500;
}

.btn {
  width: 100%;
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.btn button {
  padding: 10px 30px;
  font-weight: 550;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
}